html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  font-style: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

img, picture {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  text-transform: none;
  color: #000000;
}

h1, h2, h3 {
  font-weight: 300;
  text-align: center;
}

p {
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
  padding: 0;
  margin: 0;
  font-size: inherit;
  line-height: inherit;
}

a {
  display: inline-block;
  width: auto;
  color: inherit;
  cursor: pointer;
  transition: all 0.4s ease;
  text-decoration: none;
}

.o-container {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.o-col-100 {
  width: 100%;
  padding: 1rem;
}
.o-col-90 {
  width: 90%;
  padding: 1rem;
}
.o-col-50 {
  width: 50%;
  padding: 1rem;
}

o-font-h1 {
  color: black;
}
o-font-h2 {
  color: black;
}
o-font-h3 {
  color: black;
}
o-font-h4 {
  color: black;
}
o-font-h5 {
  color: black;
}
o-font-h6 {
  color: black;
}
o-font-p {
  color: #3434e4;
}
o-font-p--mod {
  color: #fff;
}

.o-bg-gradient {
  background: rgb(0, 95, 121);
  background: linear-gradient(180deg, rgb(0, 95, 121) 0%, rgba(77, 168, 194, 0.6951155462) 71%);
}

.c-header {
  display: block;
}
.c-header__container {
  padding: 1rem 1rem 1.5rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .c-header__container {
    padding: 1.5rem;
  }
}
.c-header__logo {
  display: block;
  margin: 0 2rem;
  max-width: 20rem;
}
@media (min-width: 768px) {
  .c-header__logo {
    max-width: 40rem;
    margin: auto;
  }
}
.c-header__nav-container {
  width: 89%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 420px) {
  .c-header__nav-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
  }
}
.c-header__nav-button {
  font-size: 1.4rem;
  transition: all 0.4s ease;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}
@media (min-width: 420px) {
  .c-header__nav-button {
    display: none;
  }
}
.c-header__nav {
  transition: all 4s ease;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
}
.c-header__nav--visible {
  height: auto;
  opacity: 1;
}
.c-header__nav--noVisible {
  height: 0;
  opacity: 0;
  display: none;
}
@media (min-width: 420px) {
  .c-header__nav {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    height: auto;
    opacity: 1;
  }
  .c-header__nav-link {
    font-size: 1.1rem;
  }
}
@media (min-width: 768px) {
  .c-header__nav {
    padding: 1rem 0;
  }
  .c-header__nav-link {
    font-size: 1.6rem;
  }
}
.c-header__nav-link {
  padding: 0.3rem 1.5rem;
  color: #ffffff;
  font-size: 1rem;
}
.c-header__nav-link:hover {
  color: #f8e912;
  font-weight: bold;
}

.c-emisora {
  display: block;
}
.c-emisora__container {
  padding: 0;
}
@media (min-width: 768px) {
  .c-emisora__container {
    padding: 3rem 0;
  }
}
.c-emisora__container2 {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  border-radius: 1.5rem;
  background-color: rgba(46, 43, 43, 0.8705882353);
  border: 1px solid #000000;
}
@media (min-width: 420px) {
  .c-emisora__container2 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    padding: 1rem 2rem;
  }
}
@media (min-width: 768px) {
  .c-emisora__container2 {
    width: 75%;
  }
}
@media (min-width: 1024px) {
  .c-emisora__container2 {
    width: 50%;
  }
}
.c-emisora__online {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 420px) {
  .c-emisora__online {
    height: 100%;
    grid-column: span 2;
    padding: 1rem 0;
  }
}
@media (min-width: 768px) {
  .c-emisora__online {
    padding: 2rem;
  }
}
.c-emisora__direct {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  margin: 1rem auto;
}
.c-emisora__direct-picture {
  margin: 1rem;
  max-width: 10rem;
}
.c-emisora__direct-picture-img {
  border-radius: 1.5rem;
}
.c-emisora__direct-text {
  font-size: 1.3rem;
  padding: 1rem;
  color: #ffffff;
  text-align: center;
}
.c-emisora__direct-text p {
  padding: 0.5rem;
}
@media (min-width: 420px) {
  .c-emisora__direct-picture {
    max-width: 12rem;
  }
}
@media (min-width: 768px) {
  .c-emisora__direct-picture {
    max-width: 18rem;
  }
  .c-emisora__direct-text {
    font-size: 1.6rem;
  }
}
.c-emisora__direct-audio {
  width: 100%;
  height: auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.c-emisora__direct-audio audio {
  height: 30px;
}
.c-emisora__listTop {
  margin: 2rem 1rem 1rem 1rem;
  width: 100%;
}
@media (min-width: 420px) {
  .c-emisora__listTop {
    margin: 1rem;
  }
}
.c-emisora__listTop-tittle {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0.5rem;
  color: #3434e4;
}
@media (min-width: 420px) {
  .c-emisora__listTop-tittle {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  .c-emisora__listTop-tittle {
    font-size: 2rem;
  }
}
.c-emisora__listTop-top5 {
  padding: 0 0.5rem 0.5rem 0.5rem;
  width: 100%;
}
@media (min-width: 768px) {
  .c-emisora__listTop-top5 {
    padding: 1rem;
  }
}
.c-emisora__listTop-top5-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem;
  padding: 1.1rem 0 0.2rem 0;
  border-bottom: 0.1rem dotted #3434e4;
}
@media (min-width: 420px) {
  .c-emisora__listTop-top5-item {
    padding-top: 1.3rem;
  }
}
@media (min-width: 768px) {
  .c-emisora__listTop-top5-item {
    padding-top: 1.5rem;
    font-size: 2rem;
  }
}
.c-emisora__listTop-p {
  color: #3434e4;
}
.c-emisora__listTop-p--mod {
  color: #ffffff;
  text-transform: lowercase;
  font-size: 1.1rem;
}
@media (min-width: 768px) {
  .c-emisora__listTop-p--mod {
    font-size: 1.5rem;
  }
}
.c-emisora__listTop-link {
  cursor: pointer;
}
.c-emisora__listTop-link:hover {
  color: #f8e912;
}

.c-list {
  display: block;
  padding-top: 3rem;
}
.c-list__container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.c-list__h1 {
  border-top: 1px dashed #010101;
  border-bottom: 1px dashed #010101;
  font-size: 2rem;
}
@media (min-width: 420px) {
  .c-list__h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) {
  .c-list__h1 {
    font-size: 3.1rem;
  }
}
.c-list__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.8rem;
  padding: 1rem;
  border: 1px solid #000;
  border-radius: 2rem;
  margin: 2rem;
  background-color: #ffffff;
}
@media (min-width: 320px) {
  .c-list__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 420px) {
  .c-list__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .c-list__grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .c-list__grid {
    width: 75%;
  }
}
.c-list__grid-item {
  border-radius: 1rem;
  margin: auto;
}
.c-list__grid-item img {
  margin: auto;
  border-radius: 1rem;
  max-height: 10rem;
  cursor: pointer;
}
.c-list__grid-item img:hover {
  border: 1px solid #000;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.6);
}
.c-list__p {
  padding: 2rem;
}
.c-list__p p {
  font-size: 1.1rem;
}
@media (min-width: 420px) {
  .c-list__p {
    padding: 1.5rem;
  }
  .c-list__p p {
    font-size: 1.6rem;
  }
}

.c-footer {
  display: block;
}
.c-footer__container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
  background-color: #1d1818;
}
@media (min-width: 420px) {
  .c-footer__container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
    padding: 1.5rem 0;
  }
}
.c-footer__email {
  padding: 1rem;
}
.c-footer__email-link {
  color: #ffffff;
  font-size: 1.2rem;
}
@media (min-width: 420px) {
  .c-footer__email-link {
    font-size: 1.6rem;
  }
}
.c-footer__email-link:hover {
  color: #f8e912;
  font-size: 1.2rem;
}
@media (min-width: 420px) {
  .c-footer__email-link:hover {
    font-size: 1.6rem;
  }
}
.c-footer__social {
  padding: 1rem;
}
.c-footer__social-link {
  padding: 0 1rem;
  color: #ffffff;
}
.c-footer .fa-facebook-f:hover {
  color: #3b5998;
}
.c-footer .fa-twitter:hover {
  color: #1da1f2;
}
.c-footer .fa-instagram:hover {
  color: #3f729b;
}
.c-footer .fa-youtube:hover {
  color: #cd201f;
}

.u-rotation {
  transform: rotate(90deg);
}